import React, { Component } from "react";
import io from "socket.io-client";
import {
  TextField,
  Button,
  Paper,
  Grid,
  Typography,
  Fab,
  Hidden,
  Avatar,
  Divider,
} from "@material-ui/core";
import SendIcon from "@material-ui/icons/Send";
import "./css/chat.css";
import GroupIcon from "@material-ui/icons/Group";
import CameraAltIcon from "@material-ui/icons/CameraAlt";
import InfoIcon from "@material-ui/icons/Info";
import CloseIcon from "@material-ui/icons/Close";
import AOS from "aos";
import "aos/dist/aos.css";

class Home extends Component {
  constructor(props) {
    super(props);
    this.chatboxRef = new React.createRef();
    this.audioref = new React.createRef();
  }

  state = {
    connected: false,
    messages: [],
    src: null,
    username: "saiteja",
    activeMsg: "",
    persons: 0,
    users: [],
    dialogOpen: false,
  };
  componentDidUpdate = () => {
    this.scrollIntoViewmsg();
    window.onhashchange = () => {
      alert("gg");
    };
  };

  componentDidMount = () => {
    this.socket = io("https://chat-app-using-node.herokuapp.com"); //https://chat-app-using-node.herokuapp.com
    this.socket.on("new-other-user-connected", (name) => {
      for (var i = 0; i < 1; i++) {
        var o = {
          name: name,
          msg: "Joined",
        };
        //update the users list as other user joins
        var users = this.state.users;
        users.push(name);
        console.log(users);
        this.setState({ users });
        //update the message list as other users join
        var messages = this.state.messages;
        messages.push(o);
        this.setState({ messages });

        //add the wish messages to the messages list
        var k = {
          name: "Chat bot",
          msg: "Hello " + name + ",Welcome to the saiteja's chat room",
        };
        var messages = this.state.messages;
        messages.push(k);
        this.setState({ messages });
        this.setState({ persons: this.state.persons + 1 });
        this.audioref.current.play();
      }
    });

    //update the messages list as other users sends message
    this.socket.on("msg-by-other", (data) => {
      for (var i = 0; i < 1; i++) {
        var o = {
          name: data.name,
          msg: data.message,
        };

        var messages = this.state.messages;
        messages.push(o);
        this.setState({ messages });
        this.audioref.current.play();
      }
    });
    //update the messaages list as other users disconnects
    this.socket.on("disconnected", (name) => {
      //show the message when other user has left
      var o = {
        name: name,
        msg: "left",
      };
      var messages = this.state.messages;
      messages.push(o);
      this.setState({ messages });
      //remove the user disconnected from the chat
      var users = this.state.users;
      var otherusers = users.filter((e) => e != name);
      console.log(otherusers);
      this.setState({ users: otherusers });

      this.setState({ persons: this.state.persons - 1 });
    });
    //set the number of users in the chat room
    this.socket.on("number-of-users", (obj) => {
      var persons = Object.keys(obj).length;
      this.setState({ persons });
      var users = Object.values(obj);
      this.setState({ users });
      console.log(users);
    });
    //wish the other  user joined
    this.socket.on("wish-message", (name) => {
      var o = {
        name: "Chat Bot",
        msg: "Hello " + name + ",Welcome to the saiteja's chat room",
      };
      var messages = this.state.messages;
      messages.push(o);
      this.setState({ messages });
      this.audioref.current.play();
    });

    //when other user posts image
    this.socket.on("add-image", (data) => {
      var k = {
        name: data.name,
        msg: "image",
        src: data.image,
      };
      var messages = this.state.messages;
      messages.push(k);
      this.setState({ messages });
      this.audioref.current.play();
    });
    this.setState({ username: this.props.username });
    this.submitName();
  };

  //disconnect user when pushed to back
  componentWillUnmount = () => {
    this.socket.emit("disconnected-force", this.state.username);
  };

  //scroll last msg into view
  scrollIntoViewmsg = () => {
    this.chatboxRef.current.scrollIntoView({ behavior: "smooth" });
  };

  //submit the name
  submitName = () => {
    var o = {
      name: "You ",
      msg: "Joined",
    };
    //add the user in the list
    var users = this.state.users;
    users.push(this.props.username);
    console.log(users);
    this.setState({ users });

    //updata the messsages list when user joins
    var messages = this.state.messages;
    messages.push(o);
    this.setState({ messages });
    this.setState({ connected: true });
    //inform other users that a user has joined
    this.socket.emit("new-user-connected", this.props.username);
  };

  //send the message
  sendMessage = () => {
    if (
      this.state.activeMsg !== "" &&
      this.state.activeMsg.indexOf(" ") !== ""
    ) {
      this.scrollIntoViewmsg();
      var o = {
        name: "You",
        msg: this.state.activeMsg,
      };
      var messages = this.state.messages;
      messages.push(o);
      console.log(messages);
      this.setState({ messages });
      this.socket.emit("send-message", this.state.activeMsg);
      this.setState({ activeMsg: "" });
    }
  };

  //handle the file changed
  handleImage = (e) => {
    var freader = new FileReader();
    freader.readAsDataURL(e.target.files[0]);
    freader.onload = (event) => {
      this.setState({ src: event.target.result });
      var k = {
        name: "You",
        msg: "image",
        src: event.target.result,
      };
      var messages = this.state.messages;
      messages.push(k);
      this.setState({ messages });
      this.socket.emit("user-image", {
        name: this.props.username,
        image: event.target.result,
      });
    };
  };

  //handle the dialog
  handleUserDialog = () => {
    this.setState({ dialogOpen: !this.state.dialogOpen });
    AOS.init({ duration: 2000 });
  };
  render() {
    return (
      <>
        <Paper className="chat-paper" elevation={3}>
          <div
            data-aos="fade"
            style={
              this.state.dialogOpen ? { display: "block" } : { display: "none" }
            }
            className="user-dialog"
          >
            <audio src={require("./alert.mp3")} ref={this.audioref} />
            <Grid
              item
              container
              justify="center"
              alignItems="center"
              style={{ padding: "4px 0px" }}
            >
              <Grid item xs={2}></Grid>
              <Grid item xs={8}>
                <Typography
                  variant="body1"
                  align="center"
                  gutterBottom
                  style={{ fontWeight: "bold" }}
                >
                  {this.state.persons} Participants
                </Typography>
              </Grid>
              <Grid item xs={2}>
                <CloseIcon
                  size="small"
                  onClick={this.handleUserDialog}
                  className="close-icon"
                />
              </Grid>
            </Grid>
            <Divider />
            {this.state.users.map((e) => (
              <>
                <Grid container alignItems="center" style={{ padding: " 6px" }}>
                  <Grid item xs={2} sm={1}>
                    <Avatar
                      src="https://sda.com"
                      alt={e.toUpperCase()}
                      style={{
                        width: "35px",
                        height: "35px",
                        marginLeft: "4px",
                        color: "white",
                        backgroundColor: "rgb(48, 48, 150)",
                      }}
                    />
                  </Grid>
                  <Grid item xs={10} sm={11} className="users">
                    <Typography variant="body1" style={{ paddingLeft: "4px" }}>
                      {e}
                    </Typography>
                  </Grid>
                </Grid>
                <Divider />
              </>
            ))}
          </div>
          <Grid
            style={{
              width: "100%",
              height: "100%",
            }}
            direction="column"
          >
            <Grid
              item
              container
              className="chat-nav"
              xs={12}
              style={{ height: "10%" }}
            >
              <Grid item xs={1}>
                <GroupIcon
                  size="small"
                  style={{ color: "white", margin: "10px 13px" }}
                />
              </Grid>
              <Grid item xs={9} sm={10} style={{ paddingLeft: "10px" }}>
                <Typography
                  variant="body2"
                  style={{
                    color: "white",
                    paddingLeft: "10px",
                    paddingTop: "4px",
                  }}
                >
                  Group Chat
                </Typography>
                <Typography
                  variant="body2"
                  component="p"
                  style={{ color: "white", paddingLeft: "10px" }}
                  gutterBottom
                >
                  {this.state.persons == 1
                    ? "Only you in the chat Room"
                    : `You and ${this.state.persons - 1} others in the Room`}
                </Typography>
              </Grid>
              <Grid item xs={2} sm={1}>
                <InfoIcon
                  size="small"
                  style={{
                    color: "white",
                    cursor: "pointer",
                  }}
                  onClick={this.handleUserDialog}
                  className="info-icon"
                />
              </Grid>
            </Grid>
            <Grid
              item
              xs={12}
              style={{
                overflowY: "scroll",
                overflowX: "hidden",
                height: "80%",
              }}
            >
              {this.state.messages.map((e, index) => {
                if (e.msg == "Joined" || e.msg == "left") {
                  return (
                    <div className="joined-msg">
                      {e.name} {e.msg}
                    </div>
                  );
                } else if (e.msg == "image" && e.name == "You") {
                  return (
                    <div className="image-by-admin">
                      <img src={e.src} />
                    </div>
                  );
                } else if (e.msg == "image" && e.name != "You") {
                  return (
                    <div className="image-by-other">
                      <div className="sender-name">
                        <Typography variant="body2">{e.name}</Typography>{" "}
                      </div>
                      <img src={e.src} />
                    </div>
                  );
                } else if (e.name == "You") {
                  return (
                    <div className="msg-by-admin">
                      <Typography variant="body2">{e.msg}</Typography>
                    </div>
                  );
                } else {
                  return (
                    <div className="msg-by-other">
                      <div className="sender-name">
                        <Typography variant="body2">{e.name}</Typography>{" "}
                      </div>

                      <Typography variant="body2">{e.msg}</Typography>
                    </div>
                  );
                }
              })}
              <div style={{ width: "100%" }} ref={this.chatboxRef}></div>
            </Grid>
            <Grid
              item
              xs={12}
              style={{ height: "12%", padding: "0px 6px", paddingTop: "10px" }}
              className="bottom-grid"
            >
              <Grid
                item
                container
                xs={12}
                justify="space-between"
                alignItems="center"
                spacing={2}
              >
                <Grid item xs={1}>
                  <label htmlFor="myInput">
                    <CameraAltIcon
                      style={{
                        color: "white",
                        cursor: "pointer",
                        marginTop: "4px",
                      }}
                      size="small"
                      className="camera-icon"
                    />
                  </label>
                  <input
                    type="file"
                    style={{ display: "none" }}
                    id="myInput"
                    onChange={this.handleImage}
                    accept="image/*"
                  />
                </Grid>
                <Grid item xs={9}>
                  <input
                    autoFocus
                    type="text"
                    placeholder="Enter Message.."
                    value={this.state.activeMsg}
                    onChange={(e) => {
                      this.setState({ activeMsg: e.target.value });
                    }}
                    onKeyDown={(e) => {
                      if (e.keyCode == 13) {
                        this.sendMessage();
                      }
                    }}
                  />
                </Grid>
                <Grid item xs={2} sm={2}>
                  <Hidden xsDown>
                    <Button
                      color="primary"
                      size="small"
                      variant="contained"
                      onClick={this.sendMessage}
                    >
                      Send
                    </Button>
                  </Hidden>
                  <Hidden smUp>
                    <Fab
                      size="small"
                      color="primary"
                      style={{ width: "35px", height: "20px" }}
                      onClick={this.sendMessage}
                    >
                      <SendIcon style={{ fontSize: "0.9rem" }} color="white" />
                    </Fab>
                  </Hidden>
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        </Paper>
      </>
    );
  }
}

export default Home;
